<template>
  <div class="detail">
    <div class="article_detail">
      <h2 class="detail_title">前端工程化</h2>
      <p class="detail_foreword">目前来说，Web业务日益复杂化和多元化，前端开发已经由以WebPage模式为主转变为以WebApp模式为主了。现在随便找个前端项目，都已经不是过去的拼个页面+搞几个jQuery插件就能完成的了。工程复杂了就会产生许多问题，比如：如何进行高效的多人协作？如何保证项目的可维护性？如何提高项目的开发质量？</p>
      <ul class="libs">
        <li style="list-style: none">
          <img :src="blogImg[0]">
        </li>
      </ul>
      <p class="detail_content">前端工程化就在这个快速发展的时代下，应运而生，它存在的目的就是为了提高效率、节约成本。 工程化最初的概念来源，是从建筑行业演化使用的，在经济没有开发前，人们盖一个房子，可以根据自己的喜好和经济情况，自己就可以把房子盖起来，但是这样的房子在别人来看是没有多少参考复用的价值的。随着经济和社会的发展，后来人们开始盖高楼大厦，渐渐的建筑引入工程化概念，对于盖房子的成本和效率都有一定的规范。</p>
      <p class="detail_content">软件中的工程化，比建筑行业的工程化，有一个无法比拟的优势，就是复用性很强，在虚拟软件中，复用一套系统成本是几乎为0的，渐渐的软件开发都在遵循一套规则——软件工程。</p>
      <p class="detail_content">工程化分为三个阶段：库/框架选型、简单构建优化、JS/CSS模块化开发。</p>
      <ul class="libs">
        <li>第一阶段：库和框架选型。
          <p>现在基本上没有人完全从0开始做网站，哪怕是政府项目用个jquery都很正常吧，React/Angularjs等框架横空出世，解放了不少生产力，合理的技术选型可以为项目节省许多工程量这点毋庸置疑。</p>
          <img :src="blogImg[1]">
        </li>
        <li>第二阶段：简单构建优化。
          <p>选型之后基本上就可以开始敲码了，不过光解决开发效率还不够，必须要兼顾运行性能。前端工程进行到第二阶段会选型一种构建工具，对代码进行压缩，校验，之后再以页面为单位进行简单的资源合并。</p>
          <img :src="blogImg[2]">
        </li>
        <li>第三阶段：JS、CSS模块化开发。
          <p>分而治之是软件工程中的重要思想，是复杂系统开发和维护的基石，这点放在前端开发中同样适用。在解决了基本开发效率运行效率问题之后，前端团队开始思考维护效率，模块化是目前前端最流行的分治手段。</p>
          <img :src="blogImg[3]">
        </li>
      </ul>
      <p class="detail_content">前端工程化有四个特点：模块化、组件化、自动化、规范化。</p>
      <p class="detail_content">
        <b>模块化：</b>
        就是将一个大文件拆分成相互依赖的小文件，再进行统一的拼装和加载。在工程化之前，一直是使用js、jquery、ajax，这没有模块概念，对于开发大型且复杂的系统会有一定的限制。
      </p>
      <p class="detail_content">
        <b>组件化：</b>
        组件化≠模块化。模板化是在文件层面上，对代码和资源的拆分；组件化是在设计层面上，对于UI的拆分。页面上所有的东西都可以看成组件，页面是个大型组件，可以拆成若干个中型组件，然后中型组件还可以再拆，拆成若干个小型组件，小型组件也可以再拆，直到拆成DOM元素为止。DOM元素可以看成是浏览器自身的组件，作为组件的基本单元。
      </p>
      <p class="detail_content">
        <b>自动化：</b>
        “简单重复的工作交给机器来做”，自动化也就是有很多自动化工具代替我们来完成，例如持续集成、自动化构建、自动化部署、自动化测试等等。
      </p>
      <p class="detail_content">
        <b>规范化：</b>
        在项目规划初期制定的好坏对于后期的开发有一定影响。包括的规范有：目录结构的制定、编码规范、前后端接口规范、文档规范、组件管理、Git分支管理、Commit描述规范、定期codeReview、视觉图标规范......
      </p>
      <p class="detail_content">
        <b>总结：</b>
        前端工程化主要就是前后端分离，复杂流程拆分，使用接口，前端项目独立打包发布，代码模块化管理。这些概念在软件工程里都是很基础的常识，主要是前端发展快，需求大，变化灵活，才从简单的前端页面发展成前端工程化，这些基础概念在前端还是属于比较热乎的概念。
      </p>
