<template>
  <div class="detail">
    <div class="article_detail">
      <h2 class="detail_title">SVG 线条动画</h2>
      <p class="detail_foreword">SVG是Scalable Vector Graphics的缩写，意为可缩放矢量图形。1998年，万维网联盟成立了一个工作组，研发一种通过XML来表现矢量图形的技术——SVG！由于SVG也是一种XML文件，所以SVG也继承了XML的开放性、可移植性和交互性的优点。基于SVG的种种优势，在实际项目中能轻松解决很多复杂动画。</p>
      <ul class="libs">
        <li style="list-style: none">
          <img :src="blogImg[0]">
        </li>
      </ul>
      <p class="detail_content" style="font-weight: bold">一、SVG在实际场景中的应用</p>
      <p class="detail_content">SVG 线条动画，在一些特定的场合下可以解决使用 CSS 无法完成的动画。尤其是在进度条方面，先来看个例子：</p>
      <ul class="libs">
        <li style="list-style: none">
          <img :src="blogImg[1]">
        </li>
      </ul>
      <p class="detail_content">实现绿色进度条的代码如下：</p>
      <pre class="code"><p class="code_line"><span class="line_num">01</span><span class="code_item">&lt;hgroup class="circle-load"&gt;</span></p><p class="code_line"><span class="line_num">02</span><span class="code_item">  &lt;svg width="240px" height="240px" version="1.1" xmlns="http://www.w3.org/2000/svg"&gt;</span></p><p class="code_line"><span class="line_num">03</span><span class="code_item">    &lt;circle cx="110" cy="110" r="90" stroke-width="20" stroke="gainsboro" fill="none"&gt;&lt;/circle&gt;</span></p><p class="code_line"><span class="line_num">04</span><span class="code_item">    &lt;circle cx="110" cy="110" r="90" stroke-width="20" stroke="darkturquoise" fill="none" class="circle-load-svg"&gt;&lt;/circle&gt;</span></p><p class="code_line"><span class="line_num">05</span><span class="code_item">  &lt;/svg&gt;</span></p><p class="code_line"><span class="line_num">06</span><span class="code_item">&lt;hgroup&gt;</span></p></pre>
      <pre class="code"><p class="code_line"><span class="line_num">01</span><span class="code_item"><span class="mark-green">.circle-load</span> {</span></p><p class="code_line"><span class="line_num">02</span><span class="code_item">  position: absolute;</span></p><p class="code_line"><span class="line_num">03</span><span class="code_item">  width: 200px;</span></p><p class="code_line"><span class="line_num">04</span><span class="code_item">  height: 200px;</span></p><p class="code_line"><span class="line_num">05</span><span class="code_item">  top: 50%;</span></p><p class="code_line"><span class="line_num">06</span><span class="code_item">  left: 50%;</span></p><p class="code_line"><span class="line_num">07</span><span class="code_item">  transform: translate(-50%, -50%);</span></p><p class="code_line"><span class="line_num">08</span><span class="code_item"> }</span></p><p class="code_line"><span class="line_num">09</span><span class="code_item"><span class="mark-green">.circle-load-svg</span> {</span></p><p class="code_line"><span class="line_num">10</span><span class="code_item">  stroke-dasharray: 0 570;</span></p><p class="code_line"><span class="line_num">11</span><span class="code_item">  animation: rot 8s linear infinite;</span></p><p class="code_line"><span class="line_num">12</span><span class="code_item">}</span></p><p class="code_line"><span class="line_num">13</span><span class="code_item"><span class="mark-purple">@keyframes</span> <span class="mark-yellow">rot</span> {</span></p><p class="code_line"><span class="line_num">14</span><span class="code_item">  100% {</span></p><p class="code_line"><span class="line_num">15</span><span class="code_item">    stroke-dasharray: 570 570;</span></p><p class="code_line"><span class="line_num">16</span><span class="code_item">  }</span></p><p class="code_line"><span class="line_num">17</span><span class="code_item">}</span></p></pre>
      <p class="detail_content">上面代码中，先谈谈 <span class="mark-yellow">svg</span> 标签：</p>
      <ul class="libs">
        <li>version： 表示 &lt;svg&gt; 的版本</li>
        <li>xmlns：http://www.w3.org/2000/svg 固定值</li>
        <li>width | height： 定义 svg 画布的大小</li>
        <li>viewbox： 定义了画布上可以显示的区域，当 viewBox 的大小和 svg 不同时，viewBox 在屏幕上的显示会缩放至 svg 同等大小（暂时可以不用理解）</li>
      </ul>
      <p class="detail_content">有了 svg 标签，我们就可以愉快的在内部添加 SVG 图形了，上面，我在 svg 中定义了两个 circle 标签</p>
      <p class="detail_content" style="font-weight: bold">二、SVG 基本形状</p>
      <p class="detail_content">SVG 中定义了一些基本形状，在继续下文之前，先了解一些基本图形的标签及写法：</p>
      <ul class="libs">
        <li>矩形：rect元素会在屏幕上绘制一个矩形 。</li>
        <li>圆形：circle元素会在屏幕上绘制一个圆形。</li>
        <li>椭圆：Ellipse 是circle元素更通用的形式，你可以分别缩放圆的x半径和y半径</li>
        <li>线条：Line 绘制直线。它取两个点的位置作为属性，指定这条线的起点和终点位置。</li>
        <li>折线：Polyline是一组连接在一起的直线。因为它可以有很多的点，折线的的所有点位置都放在一个points属性中。</li>
        <li>多边形：polygon和折线很像，它们都是由连接一组点集的直线构成。不同的是，polygon的路径在最后一个点处自动回到第一个点。</li>
        <li>路径：path可能是SVG中最常见的形状。你可以用path元素绘制矩形（直角矩形或者圆角矩形）、圆形、椭圆、折线形、多边形，以及一些其他的形状。</li>
      </ul>
      <ul class="libs">
        <li style="list-style: none">
          <img :src="blogImg[2]">
        </li>
      </ul>
      <p class="detail_content" style="font-weight: bold">SVG 线条动画</p>
      <p class="detail_content">好，终于到本文的重点了。先来看下上述代码的animation样式：<span class="mark-yellow">stroke-dasharray</span> 属性。</p>
      <p class="detail_content">stroke-dasharray 可控制用来描边的点划线的图案范式。</p>
      <p class="detail_content">它是一个&lt;length&gt;和&lt;percentage&gt;数列，数与数之间用逗号或者空白隔开，指定短划线和缺口的长度。如果提供了奇数个值，则这个值的数列重复一次，从而变成偶数个值。因此，5,3,2等同于5,3,2,5,3,2。</p>
      <p class="detail_content">stroke-dasharray: 0 570;表示线框短划线和缺口的长度分别为 0 和 570，所以一开始整个图形都是被缺口占据，所以在视觉效果上长度为 0。</p>
      <p class="detail_content">然后过渡到 stroke-dasharray: 570, 570，表示线框短划线和缺口的长度分别为 570 和 570，因为整个图形的长度就是 570，所以整个进度条会被慢慢填充满。</p>
      <p class="detail_content">掌握了这个技巧后，就可以使用 stroke-dasharray 和 stroke-dashoffset 制作很多不错的交互场景：</p>
      <p class="detail_content">SVG 线条动画实现按钮交互：</p>
      <ul class="libs">
        <li style="list-style: none">
          <img :src="blogImg[3]">
        </li>
      </ul>
