<template>
  <div class="detail">
    <div class="article_detail">
      <h2 class="detail_title">Vue组件、路由的异步加载</h2>
      <p class="detail_foreword">在大型单页面应用中，当打包构建项目时，单个js、css文件会变得非常大，在用户网络较差的情况下，打开首页面就会处于无响应的状态，用户体验极差。为了解决这样的问题，我们一般都会使用webpack的代码分割和vue-router的路由懒加载功能将我们的代码分成一个个模块，并且只在需要的时候才从服务器加载一个模块。同时在加载子模块的这段时间设置loading组件，提升用户体验。</p>
      <ul class="libs">
        <li style="list-style: none">
          <img :src="blogImg[0]">
        </li>
      </ul>
      <p class="detail_content" style="font-weight: bold">具体实现方法</p>
      <p class="detail_content">1、声明一个异步加载组件的方法，基于Vue动态组件工厂函数来返回一个Promise对象</p>
      <pre class="code"><p class="code_line"><span class="line_num">01</span><span class="note">// 引入基础组件</span></p><p class="code_line"><span class="line_num">02</span><span class="code_item"><span class="mark-purple">import</span> Vue <span class="mark-purple">from</span> 'vue'</span></p><p class="code_line"><span class="line_num">03</span><span class="code_item"><span class="mark-purple">import</span> Router <span class="mark-purple">from</span> 'vue-router'</span></p><p class="code_line"><span class="line_num">04</span><span class="note">// 引入loading和error组件</span></p><p class="code_line"><span class="line_num">05</span><span class="code_item"><span class="mark-purple">import</span> routerLoading <span class="mark-purple">from</span> './components/routerLoading'</span></p><p class="code_line"><span class="line_num">06</span><span class="code_item"><span class="mark-purple">import</span> routerError <span class="mark-purple">from</span> './components/routerError'</span></p><p class="code_line"><span class="line_num">07</span><span class="note">// 定义工厂函数</span></p><p class="code_line"><span class="line_num">08</span><span class="code_item"><span class="mark-purple">function</span> <span class="mark-green">lazyLoadView</span> (<span class="mark-yellow">asyncView</span>) {</span></p><p class="code_line"><span class="line_num">09</span><span class="code_item">  <span class="mark-purple">const</span> asyncComponent = () => ({</span></p><p class="code_line"><span class="line_num">10</span><span class="code_item">    <span class="mark-yellow">component</span>: <span class="mark-yellow">asyncView</span>,             <span class="note">// 异步加载的路由组件</span></span></p><p class="code_line"><span class="line_num">11</span><span class="code_item">    <span class="mark-yellow">loading</span>: routerLoading,           <span class="note">// loading 组件</span></span></p><p class="code_line"><span class="line_num">12</span><span class="code_item">    <span class="mark-yellow">error</span>: routerError,               <span class="note">// error 组件</span></span></p><p class="code_line"><span class="line_num">13</span><span class="code_item">    <span class="mark-yellow">delay</span>: 200,                       <span class="note">// 延迟时间</span></span></p><p class="code_line"><span class="line_num">14</span><span class="code_item">    <span class="mark-yellow">timeout</span>: 15000                    <span class="note">// 超时时间</span></span></p><p class="code_line"><span class="line_num">15</span><span class="code_item">  })</span></p><p class="code_line"><span class="line_num">16</span><span class="code_item">  <span class="mark-purple">return</span> <span class="mark-yellow">Promise</span>.<span class="mark-green">resolve</span>({</span></p><p class="code_line"><span class="line_num">17</span><span class="code_item">    <span class="mark-yellow">functional</span>: <span class="mark-purple">true</span>,</span></p><p class="code_line"><span class="line_num">18</span><span class="code_item">    <span class="mark-green">render</span> (<span class="mark-yellow">h</span>, { <span class="mark-yellow">data</span>, <span class="mark-yellow">children</span> }) {</span></p><p class="code_line"><span class="line_num">19</span><span class="code_item">      <span class="mark-purple">return</span> <span class="mark-yellow">h</span>(<span class="mark-green">asyncComponent</span>, <span class="mark-yellow">data</span>, <span class="mark-yellow">children</span>)</span></p><p class="code_line"><span class="line_num">20</span><span class="code_item">    }</span></p><p class="code_line"><span class="line_num">21</span><span class="code_item">  })</span></p><p class="code_line"><span class="line_num">22</span><span class="code_item">}</span></p></pre>
      <p class="detail_content">2、异步引入路由组件</p>
      <pre class="code"><p class="code_line"><span class="line_num">01</span><span class="code_item"><span class="mark-purple">const</span> <span class="mark-green">helloWorld</span> = () => <span class="mark-green">lazyLoadView</span>(<span class="mark-purple">import</span>('<span class="mark-yellow">./views/helloWorld</span>'))</span></p></pre>
      <p class="detail_content">3、配置routes选项</p>
      <pre class="code"><p class="code_line"><span class="line_num">01</span><span class="code_item">Vue.<span class="mark-green">use</span>(Router)</span></p><p class="code_line"><span class="line_num">02</span><span class="code_item"><span class="mark-purple">export default new</span> Router({</span></p><p class="code_line"><span class="line_num">03</span><span class="code_item">  <span class="mark-yellow">routes</span>: [</span></p><p class="code_line"><span class="line_num">04</span><span class="code_item">    {</span></p><p class="code_line"><span class="line_num">05</span><span class="code_item">      <span class="mark-yellow">path</span>: '/helloWorld',</span></p><p class="code_line"><span class="line_num">06</span><span class="code_item">      <span class="mark-yellow">name</span>: 'helloWorld',</span></p><p class="code_line"><span class="line_num">07</span><span class="code_item">      <span class="mark-yellow">component</span>: <span class="mark-green">helloWorld</span></span></p><p class="code_line"><span class="line_num">08</span><span class="code_item">    }</span></p><p class="code_line"><span class="line_num">09</span><span class="code_item">  ]</span></p><p class="code_line"><span class="line_num">10</span><span class="code_item">})</span></p></pre>
      <p class="detail_content">至此，基本配置已经完成，当你首次加载某一个组件的资源时，就会显示你在loading组件的内容，而当超出超时时间仍未加载完成该组件时，那么将显示error组件的内容。</p>
      <div class="detail_means">
        <a href="https://blog.csdn.net/sir1241/article/details/82461263" target="_blank">
          原文链接：https://blog.csdn.net/sir1241/article/details
        </a>
        <p>
          发表时间：2018-09-14
        </p>
      </div>
      <div class="close" @click="closeMe" title="关闭">×</div>
      <div class="to_top" @click="toTop" title="至顶">&#xe8fc;</div>
    </div>
  </div>
</template>

