,3,2等同于5,3,2,5,3,2。 stroke-dasharray: 0 570;表示线框短划线和缺口的长度分别为 0 和 570，所以一开始整个图形都是被缺口占据，所以在视觉效果上长度为 0。\n        ")])]),_c('p',{staticClass:"article_tab"},[_c('b',[_vm._v("")]),_c('span',{style:({backgroundColor: _vm.tabColor[6]})},[_vm._v("svg")]),_c('a',{on:{"click":function($event){_vm.showArticle(5)}}},[_vm._v("阅读全文")])])]),_c('div',{staticClass:"article"},[_vm._m(5),_c('div',{staticClass:"article_content"},[_c('div',{staticClass:"article_img",style:({backgroundImage: _vm.articleImages[5]})}),_c('p',{staticClass:"article_paragraph"},[_vm._v("\n          数据可视化，是指将相对晦涩的的数据通过可视的、交互的方式进行展示，从而形象、直观地表达数据蕴含的信息和规律。早期的数据可视化作为咨询机构、金融企业的专业工具，其应用领域较为单一，应用形态较为保守。步入大数据时代，各行各业对数据的重视程度与日俱增，随之而来的是对数据进行一站式整合、挖掘、分析、可视化的需求日益迫切，数据可视化呈现出愈加旺盛的生命力，表现之一就是视觉元素越来越多样，从朴素的柱状图/饼状图/折线图，扩展到地图、气泡图、树图、仪表盘等各式图形。表现之二是可用的开发工具越来越丰富，从专业的数据库/财务软件，扩展到基于各类编程语言的可视化库，相应的应用门槛也越来越低。数据可视化，不仅仅是统计图表。本质上，任何能够借助于图形的方式展示事物原理、规律、逻辑的方法都叫数据可视化。数据可视化不仅是一门包含各种算法的技术, 还是一个具有方法论的学科。一般而言，完整的可视化流程包括以下内容：\n        ")])]),_c('p',{staticClass:"article_tab"},[_c('b',[_vm._v("")]),_c('span',{style:({backgroundColor: _vm.tabColor[10]})},[_vm._v("大数据")]),_c('a',{on:{"click":function($event){_vm.showArticle(6)}}},[_vm._v("阅读全文")])])]),_c('keep-alive',[(_vm.show)?_c(_vm.childComponents,{tag:"p",on:{"close":_vm.closeItem}}):_vm._e()])],1),_c('div',{staticClass:"side_bar",style:({backgroundImage: _vm.sideImage})},[_c('div',{staticClass:"side_title"},[_c('span',[_vm._v("个人博客")]),_c('div',{staticClass:"link_bar"},[_c('a',{staticClass:"link_icon",style:({color: _vm.iconColor[0]}),attrs:{"href":"#"}},[_vm._v("")]),_c('a',{staticClass:"link_icon",style:({color: _vm.iconColor[1]}),attrs:{"href":"#"}},[_vm._v("")]),_c('a',{staticClass:"link_icon",style:({color: _vm.iconColor[2]}),attrs:{"href":"#"}},[_vm._v("")]),_c('a',{staticClass:"link_icon",style:({color: _vm.iconColor[3]}),attrs:{"href":"#"}},[_vm._v("")])])]),_c('div',{staticClass:"calendar"},[_c('p',{staticClass:"month"},[_c('b',{staticClass:"set_year"},[_vm._v(_vm._s(_vm.calendar