<template>
  <div class="detail">
    <div class="article_detail">
      <h2 class="detail_title">css3黑科技-滤镜</h2>
      <p class="detail_foreword">了解摄影活着美图秀秀之类美图软件的同学对滤镜肯定不陌生，本文所描述的滤镜，指的是 CSS3 出来后的滤镜，不是 IE 系列时代的滤镜，CSS3 对各种滤镜效果的支持，可以做出很多好玩儿效果。</p>
      <p class="detail_content" style="font-weight: bold">基本用法</p>
      <p class="detail_content">先简单看看几种滤镜的效果： </p>
      <ul class="libs">
        <li style="list-style: none">
          <img :src="blogImg[0]">
        </li>
      </ul>
      <p class="detail_content">简单来说，CSS 滤镜就是提供类似 PS 的图形特效，像模糊，锐化或元素变色等功能。通常被用于调整图片，背景和边界的渲染。本文就会围绕这些滤镜展开，看看具体能怎么使用或者玩出什么花样。</p>
      <p class="detail_content" style="font-weight: bold">高级用法</p>
      <p class="detail_content">既然标题是css3黑科技，那么比较常用的一些用法就不再赘述，通常我们见得比较多的 CSS 滤镜用法有：</p>
      <ul class="libs">
        <li>使用 filter: blur()生成毛玻璃效果</li>
        <li>使用 filter: drop-shadow()生成整体阴影效果</li>
        <li>使用 filter: opacity()生成透明度</li>
      </ul>
      <p class="detail_content">如果对上面的技巧不是很了解，可以稍稍百度谷歌一下，下文将由浅及深，介绍一些不大常见的滤镜的具体用法及一些小细节：</p>
      <p class="detail_content" style="font-weight: bold">blur — 生成图像阴影</p>
      <p class="detail_content">通常而言，我们生成阴影的方式大多是 box-shadow 、filter: drop-shadow() 、text-shadow 。但是，使用它们生成阴影是阴影只能是单色的。有读者同学会问了，你这么说，难道还可以生成渐变色的阴影不成？其实通过巧妙的利用 filter: blur 假装生成渐变色或者说是颜色丰富的阴影效果。</p>
      <p class="detail_content">假设我们有下述这样一张头像图片：</p>
      <ul class="libs">
        <li style="list-style: none">
          <img :src="blogImg[1]">
        </li>
      </ul>
      <p class="detail_content">下面就利用滤镜，给它添加一层与原图颜色相仿的阴影效果，核心 CSS 代码如下：</p>
      <pre class="code"><p class="code_line"><span class="line_num">01</span><span class="code_item"><span class="mark-green">.avator</span> {</span></p><p class="code_line"><span class="line_num">02</span><span class="code_item">    position: relative;</span></p><p class="code_line"><span class="line_num">03</span><span class="code_item">    width: 200px;</span></p><p class="code_line"><span class="line_num">04</span><span class="code_item">    height: 200px;</span></p><p class="code_line"><span class="line_num">05</span><span class="code_item">    background: url($img) no-repeat center center;</span></p><p class="code_line"><span class="line_num">06</span><span class="code_item">    background-size: 100% 100%;</span></p><p class="code_line"><span class="line_num">07</span><span class="code_item">    border-radius: 50%;</span></p><p class="code_line"><span class="line_num">08</span><span class="code_item">}</span></p><p class="code_line"><span class="line_num">09</span><span class="code_item"><span class="mark-green">.avator::after</span> {</span></p><p class="code_line"><span class="line_num">10</span><span class="code_item">    content: "";</span></p><p class="code_line"><span class="line_num">11</span><span class="code_item">    position: absolute;</span></p><p class="code_line"><span class="line_num">12</span><span class="code_item">    top: 10%;</span></p><p class="code_line"><span class="line_num">13</span><span class="code_item">    width: 100%;</span></p><p class="code_line"><span class="line_num">14</span><span class="code_item">    height: 100%;</span></p><p class="code_line"><span class="line_num">15</span><span class="code_item">    background: inherit;</span></p><p class="code_line"><span class="line_num">16</span><span class="code_item">    background-size: 100% 100%;</span></p><p class="code_line"><span class="line_num">17</span><span class="code_item">    filter: blur(10px) brightness(80%) opacity(.8);</span></p><p class="code_line"><span class="line_num">18</span><span class="code_item">    z-index: -1;</span></p><p class="code_line"><span class="line_num">19</span><span class="code_item">}</span></p></pre>
      <p class="detail_content">看看效果：</p>
      <ul class="libs">
        <li style="list-style: none">
          <img :src="blogImg[2]">
        </li>
      </ul>
      <p class="detail_content">其简单的原理就是，利用伪元素，生成一个与原图一样大小的新图叠加在原图之下，然后利用滤镜模糊 filter: blur() 配合其他的亮度/对比度，透明度等滤镜，制作出一个虚幻的影子，伪装成原图的阴影效果。</p>
      <p class="detail_content" style="font-weight: bold">blur 混合 contrast 产生融合效果</p>
      <p class="detail_content">接下来介绍的这个，是本文的重点，模糊滤镜叠加对比度滤镜产生的融合效果。让你知道什么是 CSS 黑科技！</p>
      <p class="detail_content">单独将两个滤镜拿出来，它们的作用分别是：</p>
      <ul class="libs">
        <li>filter: blur()： 给图像设置高斯模糊效果。</li>
        <li>filter: contrast()： 调整图像的对比度。</li>
      </ul>
      <p class="detail_content">但是，当他们“合体”的时候，产生了奇妙的融合现象，通过对比度滤镜把高斯模糊的模糊边缘给干掉，利用高斯模糊实现融合效果。</p>
      <p class="detail_content">先来看一个简单的例子：</p>
      <ul class="libs">
        <li style="list-style: none">
          <img :src="blogImg[3]">
        </li>
      </ul>
      <p class="detail_content">仔细看两圆相交的过程，在边与边接触的时候，会产生一种边界融合的效果。 上述效果的实现基于两点： </p>
      <ul class="libs">
        <li>图形是在被设置了 filter: contrast()的画布背景上进行动画的 </li>
        <li>进行动画的图形被设置了 filter: blur()（ 进行动画的图形的父元素需要是被设置了 filter: contrast() 的画布）</li>
      </ul>
      <p class="detail_content">意思是，上面两圆运动的背后，其实是叠加了一张设置了 filter: contrast() 的大白色背景，而两个圆形则被设置了 filter: blur() ,两个条件缺一不可。</p>
      <p class="detail_content" style="font-weight: bold">燃烧的火焰</p>
      <p class="detail_content">好，上面介绍完原理，下面看看使用这个效果制作的一些强大 CSS 效果，其中最为惊艳的就是使用融合效果制作生成火焰，这个效果我最早是见于 Yusuke Nakaya 这位作者：</p>
      <ul class="libs">
        <li style="list-style: none">
          <img :src="blogImg[4]">
        </li>
      </ul>
      <p class="detail_content">不用怀疑你的眼睛，上述 GIF 效果就是使用纯 CSS 实现的。核心还是 filter: contrast() 与 filter: blur() 配合使用，不过实现的过程也非常有趣，我们需要使用 CSS 画出一个火焰形状。</p>
      <p class="detail_content">火焰形状 CSS 核心代码如下：</p>
      <pre class="code"><p class="code_line"><span class="line_num">01</span><span class="code_item"><span class="mark-green">.fire</span> {</span></p><p class="code_line"><span class="line_num">02</span><span class="code_item">  width: 0;</span></p><p class="code_line"><span class="line_num">03</span><span class="code_item">  height: 0;</span></p><p class="code_line"><span class="line_num">04</span><span class="code_item">  border-radius: 45%;</span></p><p class="code_line"><span class="line_num">05</span><span class="code_item">  box-sizing: border-box;</span></p><p class="code_line"><span class="line_num">06</span><span class="code_item">  border: 100px solid #000;</span></p><p class="code_line"><span class="line_num">07</span><span class="code_item">  border-bottom: 100pxsolid transparent;</span></p><p class="code_line"><span class="line_num">08</span><span class="code_item">  background-color: #b5932f;</span></p><p class="code_line"><span class="line_num">09</span><span class="code_item">  transform: scaleX(.4);</span></p><p class="code_line"><span class="line_num">10</span><span class="code_item">  filter: blur(20px) contrast(30);</span></p><p class="code_line"><span class="line_num">11</span><span class="code_item">}</span></p></pre>
      <p class="detail_content">最后，我们只需要在火焰 .fire 这个 div 内部，用大量的黑色圆形，由下至上，无规律穿过火焰即可。由于滤镜的融合效果，火焰效果随之产生。</p>
      <p class="detail_content" style="font-weight: bold">值得注意的细节点</p>
      <p class="detail_content">动画虽然美好，但是具体使用的过程中，仍然有一些需要注意的地方：</p>
      <p class="detail_content">1. CSS 滤镜可以给同个元素同时定义多个，例如 filter: contrast(150%) brightness(1.5) ，但是滤镜的先后顺序不同产生的效果也是不一样的，也就是说，使用 filter: contrast(150%) brightness(1.5) 和 filter: brightness(1.5) contrast(150%) 处理同一张图片，得到的效果是不一样的，原因在于滤镜的色值处理算法对图片处理的先后顺序；</p>
      <p class="detail_content">2. 滤镜动画需要大量的计算，不断的重绘页面，属于非常消耗性能的动画，使用时要注意使用场景。记得开启硬件加速及合理使用分层技术；</p>
      <p class="detail_content">3. CSS3 filter 兼容性不算太好，但是在移动端已经可以比较正常的使用，更为精确的兼容性列表，查询 Can i Use。</p>
