var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"detail"},[_c('div',{staticClass:"article_detail"},[_c('h2',{staticClass:"detail_title"},[_vm._v("数据可视化")]),_c('p',{staticClass:"detail_foreword"},[_vm._v("数据可视化，是指将相对晦涩的的数据通过可视的、交互的方式进行展示，从而形象、直观地表达数据蕴含的信息和规律。早期的数据可视化作为咨询机构、金融企业的专业工具，其应用领域较为单一，应用形态较为保守。步入大数据时代，各行各业对数据的重视程度与日俱增，随之而来的是对数据进行一站式整合、挖掘、分析、可视化的需求日益迫切，数据可视化呈现出愈加旺盛的生命力，表现之一就是视觉元素越来越多样，从朴素的柱状图/饼状图/折线图，扩展到地图、气泡图、树图、仪表盘等各式图形。表现之二是可用的开发工具越来越丰富，从专业的数据库/财务软件，扩展到基于各类编程语言的可视化库，相应的应用门槛也越来越低。")]),_c('ul',{staticClass:"libs"},[_c('li',{staticStyle:{"list-style":"none"}},[_c('img',{attrs:{"src":_vm.blogImg[0]}})])]),_c('p',{staticClass:"detail_content"},[_vm._v("数据可视化，不仅仅是统计图表。本质上，任何能够借助于图形的方式展示事物原理、规律、逻辑的方法都叫数据可视化。")]),_c('p',{staticClass:"detail_content"},[_vm._v("数据可视化不仅是一门包含各种算法的技术, 还是一个具有方法论的学科。一般而言，完整的可视化流程包括以下内容：")]),_vm._m(0),_c('p',{staticClass:"detail_content"},[_vm._v("实际上，从“数据可视化”的命名，便很容易看出数据可视化从业者如何开始可视化设计，那便是：处理数据，设计视觉，完成从数据空间到可视空间的映射, 必要时重复数据处理和图形绘制的循环组合。")]),_vm._m(1),_c('p',{staticClass:"detail_content"},[_vm._v("首先，我们需要对数据做一个全面而细致的解读，数据的特点决定着可视化的设计原则。每项数据都有特定的属性（或称特征、维度）和对应的值，一组属性构成特征列表。按照属性的类型，数据可以分为数值型、有序型、类别型，数值型又可以进一步分为固定零点和非固定零点。其中，固定零点数据囊括了我们大多数的数据对象，它们都可以对应到数轴上的某个点；非固定零点主要包括以数值表示的特定含义，如表示地理信息的经纬度、表示日期的年月日等，在分析非固定零点数据时，我们更在意的是它们的区间。")]),_c('p',{staticClass:"detail_content"},[_vm._v("在对数据做过预处理和分析之后，我们就能够观察出待处理数据的分布和维度，再结合业务逻辑和可视化目标，有可能还要对数据做某些变换，这些变换包括：")]),_vm._m(2),_vm._m(3),_c('p',{staticClass:"detail_content"},[_vm._v("在开始设计之前，我们需要对人类视觉以及注意力作简要分析，这决定着我们如何在第一时间抓住受众的注意力。")]),_c('p',{staticClass:"detail_content"},[_vm._v("人类视觉感知到心理认知的过程要经过信息的获取、分析、归纳、解码、储存、概念、提取、使用等一系列加工阶段，每个阶段需要不同的人体组织和器官参与。简单来讲，人类视觉的特点是：")]),_vm._m(4),_c('p',{staticClass:"detail_content"},[_vm._v("这里引入一个概念——可视编码，它数据信息映射为可视化元素的技术，其通常具有表达直观、易于理解和记忆的特性。数据包含属性和值，相应可视编码也由两部分组成：标记和视觉通道，标记代表数据属性的分类，视觉通道表示人眼所能看到的各种元素的属性，包括大小、形状、颜色等，往往用来展示属性的定量信息。例如，对于柱状图而言，标记就是矩形，视觉通道就是矩形的颜色、高度或宽度等。")]),_c('p',{staticClass:"detail_content"},[_vm._v("数据可视化的设计目标和制作原则在于信、达、雅，即一要精准展现数据的差异、趋势、规律，二要准确传递核心思想，三要简洁美观，不携带冗余信息。结合人的视觉特点，很容易总结出好的数据可视化作品的基本特征：")]),_vm._m(5),_vm._m(6),_c('p',{staticClass:"detail_content"},[_vm._v("统计图表是使用最早的可视化图形，在数百年的进化过程中，逐渐形成了基本“套路”，符合人类感知和认知，进而被广泛接受。")]),_c('p',{staticClass:"detail_content"},[_vm._v("常见于各种分析报告的有柱状图、折线图、饼图、散点图、气泡图、雷达图，对于这些最常用的图表类型，下表可以为你指明大致方向：")]),_c('ul',{staticClass:"libs"},[_c('li',{staticStyle:{"list-style":"none"}},[_c('img',{attrs:{"src":_vm.blogImg[1]}})])]),_c('p',{staticClass:"detail_content"},[_vm._v("我们在制作可视化图表时，首先要从业务出发，优先挑选合理的、符合惯例的图表，尤其是如果你的用户层次比较多样的情况下，请兼顾各个年龄段或者不同认知能力的用户的需求；其次是根据数据的各种属性和统计图表的特点来选择，例如饼图并不适合用作展示绝对数值，只适用于反映各部分的比例。对于常用图表，带着目的出发，遵循各种约束，相信你能找到合适的图表。除了常用的图表之外，可供我们选择的还有：")]),_vm._m(7),_c('p',{staticClass:"detail_content"},[_vm._v("结语：我们所处的这个时代，是一个知识大爆发的时代。仅就编程这项技能来说，现在几乎人人都能写上一两行，程序员这个群体也比十年前有了更多维度上的扩展。各种工具的使用门槛越来越低，带来的是行业壁垒日渐消弭。会用某种可视化手段，并不是区分小白与专家的唯一标准，笔者曾听说有人拿unity、易语言这样的神器来实现可视化。所以，眼花缭乱的工具永远只是辅助，真正决定你在可视化道路上走多远的，是你对可视化理念的准确把握和执着坚守。")]),_vm._m(8),_c('div',{staticClass:"close",attrs:{"title":"关闭"},on:{"click":_vm.closeMe}},[_vm._v("×")]),_c('div',{staticClass:"to_top",attrs:{"title":"至顶"},on:{"click":_vm.toTop}},[_vm._v("")])])])}
