<template>
  <div class="detail">
    <div class="article_detail">
      <h2 class="detail_title">Vue服务端渲染(SSR)</h2>
      <p class="detail_foreword"> 随着VUE的发展，国内很多网络产品选用了VUE作为开发，但国内百度蜘蛛无内存抓取严重了影响了VUE的发展，VUE在2017年也大力的推荐SSR技术</p>
      <ul class="libs">
        <li style="list-style: none">
          <img :src="blogImg[0]">
        </li>
      </ul>
      <p class="detail_content">什么是服务端渲染，简单理解是将组件或页面通过服务器生成html字符串，再发送到浏览器，最后将静态标记"混合"为客户端上完全交互的应用程序。 于传统的SPA（单页应用）相比，服务端渲染能更好的有利于SEO，减少页面首屏加载时间，当然对开发来讲我们就不得不多学一些知识来支持服务端渲染。同时服务端渲染对服务器的压力也是相对较大的，和服务器简单输出静态文件相比，通过node去渲染出页面再传递给客户端显然开销是比较大的，需要注意准备好相应的服务器负载。</p>
      <p class="detail_content">一、一个简单的例子</p>
      <pre class="code"><p class="code_line"><span class="line_num">01</span><i class="note">// 第 1 步：创建一个 Vue 实例</i></p><p class="code_line"><span class="line_num">02</span><span class="code_item"><span class="mark-purple">const</span> Vue = <span class="mark-yellow">require</span>('<span class="mark-green">vue</span>')</span></p><p class="code_line"><span class="line_num">03</span><span class="code_item"><span class="mark-purple">const</span> app = <span class="mark-purple">new</span> Vue({</span></p><p class="code_line"><span class="line_num">04</span><span class="code_item">  <span class="mark-yellow">template</span>: <span class="mark-green">`&lt;div&gt;Hello World&lt;/div&gt;`</span></span></p><p class="code_line"><span class="line_num">05</span><span class="code_item">})</span></p><p class="code_line"><span class="line_num">06</span><i class="note">// 第 2 步：创建一个 renderer</i></p><p class="code_line"><span class="line_num">07</span><span class="code_item"><span class="mark-purple">const</span> renderer = <span class="mark-yellow">require</span>('<span class="mark-green">vue-server-renderer</span>').createRenderer()</span></p><p class="code_line"><span class="line_num">08</span><i class="note">// 第 3 步：将 Vue 实例渲染为 HTML</i></p><p class="code_line"><span class="line_num">09</span><span class="code_item">renderer.renderToString(<span class="mark-green">app</span>, (<span class="mark-yellow">err</span>, <span class="mark-yellow">html</span>) => {</span></p><p class="code_line"><span class="line_num">10</span><span class="code_item">  <span class="mark-purple">if</span> (<span class="mark-yellow">err</span>) <span class="mark-purple">throw</span> <span class="mark-yellow">err</span></span></p><p class="code_line"><span class="line_num">11</span><span class="code_item">  console.log(<span class="mark-yellow">html</span>)</span></p><p class="code_line"><span class="line_num">12</span><span class="code_item">})</span></p></pre>
      <p class="detail_content">上面例子利用 vue-server-renderer npm 包将一个vue示例最后渲染出了一段 html。将这段html发送给客户端就轻松的实现了服务器渲染了。</p>
      <pre class="code"><p class="code_line"><span class="line_num">01</span><span class="code_item"><span class="mark-purple">const</span> server = <span class="mark-yellow">require</span>('<span class="mark-green">express</span>')()</span></p><p class="code_line"><span class="line_num">02</span><span class="code_item">server.get('<span class="mark-green">*</span>', (req, res) => {</span></p><p class="code_line"><span class="line_num">03</span><span class="note">  // ... 生成 html</span></p><p class="code_line"><span class="line_num">04</span><span class="code_item">  res.end(html)</span></p><p class="code_line"><span class="line_num">05</span><span class="code_item">})</span></p><p class="code_line"><span class="line_num">06</span><span class="code_item">server.listen(<span class="mark-yellow">8080</span>)</span></p></pre>
      <p class="detail_content">二、官方渲染步骤</p>
      <p class="detail_content">上面例子虽然简单，但在实际项目中往往还需要考虑到路由，数据，组件化等等，所以服务端渲染不是只用一个 vue-server-renderer npm包就能轻松搞定的，下面给出一张Vue官方的服务器渲染示意图：</p>
      <ul class="libs">
        <li style="list-style: none">
          <img :src="blogImg[1]">
        </li>
      </ul>
      <p class="detail_content">流程图大致意思是：将 Source（源码）通过 webpack 打包出两个 bundle，其中 Server Bundle 是给服务端用的，服务端通过渲染器 bundleRenderer 将 bundle 生成 html 给浏览器用；另一个 Client Bundle 是给浏览器用的，别忘了服务端只是生成前期首屏页面所需的 html ，后期的交互和数据处理还是需要能支持浏览器脚本的 Client Bundle 来完成。</p>
      <p class="detail_content">三、具体实现方法</p>
      <p class="detail_content" style="font-weight: bold">1、先实现一个基本版</p>
      <p class="detail_content">项目结构示例：</p>
      <pre class="code"><p class="code_line"><span class="line_num">01</span><span class="code_item">├── build</span></p><p class="code_line"><span class="line_num">02</span><span class="code_item">│   ├── webpack.base.config.js     <span class="note"># 基本配置文件</span></span></p><p class="code_line"><span class="line_num">03</span><span class="code_item">│   ├── webpack.client.config.js   <span class="note"># 客户端配置文件</span></span></p><p class="code_line"><span class="line_num">04</span><span class="code_item">│   ├── webpack.server.config.js   <span class="note"># 服务端配置文件</span></span></p><p class="code_line"><span class="line_num">05</span><span class="code_item">└── src</span></p><p class="code_line"><span class="line_num">06</span><span class="code_item">    ├── router</span></p><p class="code_line"><span class="line_num">07</span><span class="code_item">    │    └── index.js              <span class="note"># 路由</span></span></p><p class="code_line"><span class="line_num">08</span><span class="code_item">    └── views</span></p><p class="code_line"><span class="line_num">09</span><span class="code_item">    │    ├── comp1.vue             <span class="note"># 组件</span></span></p><p class="code_line"><span class="line_num">10</span><span class="code_item">    │    ├── comp2.vue             <span class="note"># 组件</span></span></p><p class="code_line"><span class="line_num">11</span><span class="code_item">    ├── App.vue                    <span class="note"># 顶级vue组件</span></span></p><p class="code_line"><span class="line_num">12</span><span class="code_item">    ├── app.js                     <span class="note"># app 入口文件</span></span></p><p class="code_line"><span class="line_num">13</span><span class="code_item">    ├──  client-entry.js           <span class="note"># client 的入口文件</span></span></p><p class="code_line"><span class="line_num">14</span><span class="code_item">    ├──  index.template.html       <span class="note"># html 模板</span></span></p><p class="code_line"><span class="line_num">15</span><span class="code_item">    ├──  server-entry.js           <span class="note"># server 的入口文件</span></span></p><p class="code_line"><span class="line_num">16</span><span class="code_item">├──  server.js                     <span class="note"># # server 服务</span></span></p></pre>
      <p class="detail_content">其中：</p>
      <p class="detail_content">（1）、comp1.vue 和 copm2.vue 组件</p>
      <pre class="code"><p class="code_line"><span class="line_num">01</span><span class="code_item">&lt;template&gt;</span></p><p class="code_line"><span class="line_num">02</span><span class="code_item">  &lt;<span class="mark-yellow">section</span>&gt;组件 1&lt;/<span class="mark-yellow">section</span>&gt;</span></p><p class="code_line"><span class="line_num">03</span><span class="code_item">&lt;/template&gt;</span></p><p class="code_line"><span class="line_num">04</span><span class="code_item">&lt;script&gt;</span></p><p class="code_line"><span class="line_num">05</span><span class="code_item">  <span class="mark-green">export default {</span></span></p><p class="code_line"><span class="line_num">06</span><span class="code_item">    <span class="mark-green">data () {</span></span></p><p class="code_line"><span class="line_num">07</span><span class="code_item">      <span class="mark-green">return {</span></span></p><p class="code_line"><span class="line_num">08</span><span class="code_item">        <span class="mark-green">msg: ''</span></span></p><p class="code_line"><span class="line_num">09</span><span class="code_item">      <span class="mark-green">}</span></span></p><p class="code_line"><span class="line_num">10</span><span class="code_item">    <span class="mark-green">}</span></span></p><p class="code_line"><span class="line_num">11</span><span class="code_item">  <span class="mark-green">}</span></span></p><p class="code_line"><span class="line_num">12</span><span class="code_item">&lt;/script&gt;</span></p></pre>
      <p class="detail_content">（2）、App.vue 顶级 vue 组件</p>
      <pre class="code"><p class="code_line"><span class="line_num">01</span><span class="code_item">&lt;template&gt;</span></p><p class="code_line"><span class="line_num">02</span><span class="code_item">  &lt;div id="app"&gt;</span></p><p class="code_line"><span class="line_num">03</span><span class="code_item">    &lt;h1&gt;vue-ssr&lt;/h1&gt;</span></p><p class="code_line"><span class="line_num">04</span><span class="code_item">    &lt;router-link class="link" to="/comp1"&gt;to comp1&lt;/router-link&gt;</span></p><p class="code_line"><span class="line_num">05</span><span class="code_item">    &lt;router-link class="link" to="/comp2"&gt;to comp2&lt;/router-link&gt;</span></p><p class="code_line"><span class="line_num">06</span><span class="code_item">    &lt;router-view class="view"&gt;&lt;/router-view&gt;</span></p><p class="code_line"><span class="line_num">07</span><span class="code_item">  &lt;/div&gt;</span></p><p class="code_line"><span class="line_num">08</span><span class="code_item">&lt;/template&gt;</span></p></pre>
      <p class="detail_content">（3）、index.template.html html 模板</p>
      <pre class="code"><p class="code_line"><span class="line_num">01</span><span class="code_item">&lt;!DOCTYPE html&gt;</span></p><p class="code_line"><span class="line_num">02</span><span class="code_item">&lt;html lang="zh_CN"&gt;</span></p><p class="code_line"><span class="line_num">03</span><span class="code_item">&lt;head&gt;</span></p><p class="code_line"><span class="line_num">04</span><span class="code_item">  &lt;title&gt;{ { title } }&lt;/title&gt;</span></p><p class="code_line"><span class="line_num">05</span><span class="code_item">  &lt;meta charset="utf-8"/&gt;</span></p><p class="code_line"><span class="line_num">06</span><span class="code_item">  &lt;meta name="mobile-web-app-capable" content="yes"/&gt;</span></p><p class="code_line"><span class="line_num">07</span><span class="code_item">  &lt;meta http-equiv="X-UA-Compatible" content="IE=edge, chrome=1"/&gt;</span></p><p class="code_line"><span class="line_num">08</span><span class="code_item">  &lt;meta name="renderer" content="webkit"/&gt;</span></p><p class="code_line"><span class="line_num">09</span><span class="code_item">  &lt;meta name="viewport" content="width=device-width, initial-scale=1, maximum-scale=1, user-scalable=no, minimal-ui"/&gt;</span></p><p class="code_line"><span class="line_num">10</span><span class="code_item">  &lt;meta name="theme-color" content="#f60"/&gt;</span></p><p class="code_line"><span class="line_num">11</span><span class="code_item">&lt;/head&gt;</span></p><p class="code_line"><span class="line_num">12</span><span class="code_item">&lt;body&gt;</span></p><p class="code_line"><span class="line_num">13</span><span class="note">  &lt;!--vue-ssr-outlet--&gt;</span></p><p class="code_line"><span class="line_num">14</span><span class="code_item">&lt;/body&gt;</span></p><p class="code_line"><span class="line_num">15</span><span class="code_item">&lt;/html&gt;</span></p></pre>
      <p class="detail_content">（4）、上面基础代码不解释，接下来看</p>
      <p class="detail_content">路由 router</p>
      <pre class="code"><p class="code_line"><span class="line_num">01</span><span class="code_item"><span class="mark-purple">import</span> Vue <span class="mark-purple">from</span> <span class="mark-green">'vue'</span></span></p><p class="code_line"><span class="line_num">02</span><span class="code_item"><span class="mark-purple">import</span> Router <span class="mark-purple">from</span> <span class="mark-green">'vue-router'</span></span></p><p class="code_line"><span class="line_num">03</span><span class="code_item"><span class="mark-purple">import</span> comp1 <span class="mark-purple">from</span> <span class="mark-green">'../views/comp1.vue'</span></span></p><p class="code_line"><span class="line_num">04</span><span class="code_item"><span class="mark-purple">import</span> comp2 <span class="mark-purple">from</span> <span class="mark-green">'../views/comp2.vue'</span></span></p><p class="code_line"><span class="line_num">05</span><span class="code_item">Vue.use(Router)</span></p><p class="code_line"><span class="line_num">06</span><span class="code_item"><span class="mark-purple">export function</span> <span class="mark-yellow">createRouter</span> () {</span></p><p class="code_line"><span class="line_num">07</span><span class="code_item"><span class="mark-purple">  return new</span> Router({</span></p><p class="code_line"><span class="line_num">08</span><span class="code_item"><span class="mark-yellow">    mode</span>: <span class="mark-green">'hidtory'</span></span></p><p class="code_line"><span class="line_num">09</span><span class="code_item"><span class="mark-yellow">    scrollBehavior</span>: () => ({ y: 0 }),</span></p><p class="code_line"><span class="line_num">10</span><span class="code_item"><span class="mark-yellow">    routes</span>: [</span></p><p class="code_line"><span class="line_num">11</span><span class="code_item">      {</span></p><p class="code_line"><span class="line_num">12</span><span class="code_item">        <span class="mark-yellow">path</span>: <span class="mark-green">'/comp1'</span></span></p><p class="code_line"><span class="line_num">13</span><span class="code_item">        <span class="mark-yellow">component</span>: comp1</span></p><p class="code_line"><span class="line_num">14</span><span class="code_item">      },</span></p><p class="code_line"><span class="line_num">15</span><span class="code_item">      {</span></p><p class="code_line"><span class="line_num">16</span><span class="code_item">        <span class="mark-yellow">path</span>: <span class="mark-green">'/comp2'</span></span></p><p class="code_line"><span class="line_num">17</span><span class="code_item">        <span class="mark-yellow">component</span>: comp2</span></p><p class="code_line"><span class="line_num">18</span><span class="code_item">      },</span></p><p class="code_line"><span class="line_num">19</span><span class="code_item">      {</span></p><p class="code_line"><span class="line_num">20</span><span class="code_item">        <span class="mark-yellow">path</span>: <span class="mark-green">'/'</span></span></p><p class="code_line"><span class="line_num">21</span><span class="code_item">        <span class="mark-yellow">component</span>: comp1</span></p><p class="code_line"><span class="line_num">22</span><span class="code_item">      }</span></p><p class="code_line"><span class="line_num">23</span><span class="code_item">    ]</span></p><p class="code_line"><span class="line_num">24</span><span class="code_item">  })</span></p><p class="code_line"><span class="line_num">25</span><span class="code_item">}</span></p></pre>
      <p class="detail_content">app.js app 入口文件</p>
      <pre class="code"><p class="code_line"><span class="line_num">01</span><span class="code_item"><span class="mark-purple">import</span> Vue <span class="mark-purple">from</span> <span class="mark-green">'vue'</span></span></p><p class="code_line"><span class="line_num">02</span><span class="code_item"><span class="mark-purple">import</span> app <span class="mark-purple">from</span> <span class="mark-green">'./App.vue'</span></span></p><p class="code_line"><span class="line_num">03</span><span class="code_item"><span class="mark-purple">import</span> { createRouter } <span class="mark-purple">from</span> <span class="mark-green">'./router'</span></span></p><p class="code_line"><span class="line_num">04</span><span class="code_item"><span class="mark-purple">export function</span> <span class="mark-yellow">createApp</span> (ssrContext) {</span></p><p class="code_line"><span class="line_num">05</span><span class="code_item">  <span class="mark-purple">const </span> router = createRouter()</span></p><p class="code_line"><span class="line_num">06</span><span class="code_item">  <span class="mark-purple">const </span> app = <span class="mark-purple">new</span> Vue({</span></p><p class="code_line"><span class="line_num">07</span><span class="code_item">    router,</span></p><p class="code_line"><span class="line_num">08</span><span class="code_item">    ssrContext,</span></p><p class="code_line"><span class="line_num">09</span><span class="code_item">    <span class="mark-yellow">render</span>: h => h(App)</span></p><p class="code_line"><span class="line_num">10</span><span class="code_item">  })</span></p><p class="code_line"><span class="line_num">11</span><span class="code_item">  <span class="mark-purple">return</span> { app, router }</span></p><p class="code_line"><span class="line_num">12</span><span class="code_item">}</span></p></pre>
      <p class="detail_content">我们通过 createApp 暴露一个根 Vue 实例，这是为了确保每个用户能得到一份新的实例，避免状态污染，所以我们写了一个可以重复执行的工厂函数 createApp。 同样路由 router 我们也是一样的处理方式 createRouter 来暴露一个 router 实例</p>
      <p class="detail_content">（5）client-entry.js client 的入口文件</p>
      <pre class="code"><p class="code_line"><span class="line_num">01</span><span class="code_item"><span class="mark-purple">import</span> { createApp } <span class="mark-purple">from</span> <span class="mark-green">'./app'</span></span></p><p class="code_line"><span class="line_num">02</span><span class="code_item"><span class="mark-purple">const</span> { app, router } = createApp()</span></p><p class="code_line"><span class="line_num">03</span><span class="code_item">router.onReady(() => {</span></p><p class="code_line"><span class="line_num">04</span><span class="code_item">  app.$mount('<span class="mark-green">#app</span>')</span></p><p class="code_line"><span class="line_num">05</span><span class="code_item">})</span></p></pre>
      <p class="detail_content">客户端代码是在路由解析完成的时候讲 app 挂载到 #app 标签下</p>
      <p class="detail_content">（6）server-entry.js server 的入口文件</p>
      <pre class="code"><p class="code_line"><span class="line_num">01</span><span class="code_item"><span class="mark-purple">import</span> { createApp } <span class="mark-purple">from</span> <span class="mark-green">'./app'</span></span></p><p class="code_line"><span class="line_num">02</span><span class="code_item"><span class="mark-purple">export default</span> context => {</span></p><p class="code_line"><span class="line_num">03</span><span class="note">  // 因为这边 router.onReady 是异步的，所以我们返回一个 Promise</span></p><p class="code_line"><span class="line_num">04</span><span class="note">  // 确保路由或组件准备就绪</span></p><p class="code_line"><span class="line_num">05</span><span class="code_item">  <span class="mark-purple">return new</span> <span class="mark-yellow">Promise</span>((resolve, reject) => {</span></p><p class="code_line"><span class="line_num">06</span><span class="code_item">    <span class="mark-purple">const</span> { app, router } = createApp(context)</span></p><p class="code_line"><span class="line_num">07</span><span class="code_item">    router.push(context.url)</span></p><p class="code_line"><span class="line_num">08</span><span class="code_item">    router.onReady(() => {</span></p><p class="code_line"><span class="line_num">09</span><span class="code_item">      resolve(app)</span></p><p class="code_line"><span class="line_num">10</span><span class="code_item">    }, reject)</span></p><p class="code_line"><span class="line_num">11</span><span class="code_item">  })</span></p><p class="code_line"><span class="line_num">12</span><span class="code_item">}</span></p></pre>
      <p class="detail_content" style="font-weight: bold">2、打包</p>
      <p class="detail_content">在第一步我们大费周章实现了一个带有路由的日常功能模板代码，接着我们需要利用webpack将上面的代码打包出服务端和客户端key的代码，入口文件分别是 server-entry.js 和 client-entry.js</p>
      <p class="detail_content">（1）、 webpack构建配置</p>
      <p class="detail_content">一般配置分为三个文件：base, client 和 server。基本配置(base config)包含在两个环境共享的配置，例如，输出路径(output path)，别名(alias)和 loader。服务器配置(server config)和客户端配置(client config)，可以通过使用 webpack-merge 来简单地扩展基本配置。</p>
      <p class="detail_content">（2）、 webpack build poj</p>
      <pre class="code"><p class="code_line"><span class="line_num">01</span><span class="code_item">webpack --config build/webpack.client.config.js</span></p><p class="code_line"><span class="line_num">02</span><span class="code_item">webpack --config build/webpack.server.config.js</span></p></pre>
      <p class="detail_content">打包后会生成一些打包文件，其中 server.config 打包后会生成 vue-ssr-server-bundle.json 文件，这个文件是给 createBundleRenderer 用的,用于服务端渲染出 html 文件</p>
      <pre class="code"><p class="code_line"><span class="line_num">01</span><span class="code_item"><span class="mark-purple">const</span> { createBundleRenderer } = <span class="mark-yellow">require</span>('<span class="mark-green">vue-server-renderer</span>')</span></p><p class="code_line"><span class="line_num">02</span><span class="code_item"><span class="mark-purple">const</span> renderer = createBundleRenderer('<span class="mark-green">/path/to/vue-ssr-server-bundle.json</span>', {</span></p><p class="code_line"><span class="line_num">03</span><span class="note">  // ……renderer 的其他选项</span></p><p class="code_line"><span class="line_num">04</span><span class="code_item">})</span></p></pre>
      <p class="detail_content">（3）、 服务端渲染</p>
      <p class="detail_content">其实上面都是准备工作，最重要的一步是将webpack构建后的资源代码给服务端用来生成 html 。我们需要用node写一个服务端应用，通过打包后的资源生成 html 并发送给浏览器。</p>
      <p class="detail_content">四、小结</p>
      <p class="detail_content">以上就是vue ssr的简单流程，服务端渲染还有比较重要的一部分是首屏数据的获取渲染，一般页面展示都会有一些网络数据初始化，服务端渲染可以将这些数据获取到插入到 html 。</p>
