<script>
import article1 from '../components/blog/article1'
import article2 from '../components/blog/article2'
import article3 from '../components/blog/article3'
import article4 from '../components/blog/article4'
import article5 from '../components/blog/article5'
import article6 from '../components/blog/article6'

export default {
  name: 'myBlog',
  components: {
    article1,
    article2,
    article3,
    article4,
    article5,
    article6
  },
  data () {
    return {
      sideImage: "url('./images/blog/blog_bg.jpeg')",
      articleImages: [
        "url('./images/blog/blog1-1.jpg')",
        "url('./images/blog/blog2-1.jpg')",
        "url('./images/blog/blog3-1.jpg')",
        "url('./images/blog/blog4-1.jpg')",
        "url('./images/blog/blog5-1.jpg')",
        "url('./images/blog/blog6-1.jpg')"
      ],
      childComponents: undefined,
      show: false,
      calendar: {
        currentYear: 1970,
        currentMonth: 1,
        currentDay: 1,
        daysList: []
      },
      iconColor: [
        '#E5BF21',
        '#64B41A',
        '#FA5D67',
        '#F07E27'
      ],
      tabColor: [
        '#E57E2E',
        '#F2B92C',
        '#4CAE4C',
        '#F0AD4E',
        '#BF55ED',
        '#E8C431',
        '#4D8BF0',
        '#50DBF0',
        '#2CE5A7',
        '#81E555',
        '#E54848'
      ]
    }
  },
  created () {
    this.initData()
  },
  methods: {
    initData () {
      let now = new Date()
      let y = now.getFullYear()
      let m = now.getMonth() + 1
      let d = now.getDate()
      let forwardLastDay = new Date(now.getFullYear(), now.getMonth(), 0).getDate()
      let currentLastDay = new Date(now.getFullYear(), now.getMonth() + 1, 0).getDate()
      let preLastWeek = new Date(now.getFullYear(), now.getMonth(), 0).getDay()
      let currentLastWeek = new Date(now.getFullYear(), now.getMonth() + 1, 0).getDay()
      let days = []
      for (let i = 0; i < preLastWeek + 1; i++) {
        let day = []
        day[0] = m - 1
        day[1] = forwardLastDay - preLastWeek + i
        days.push(day)
      }
      for (let i = 1; i < currentLastDay + 1; i++) {
        let day = []
        day[0] = m
        day[1] = i
        days.push(day)
      }
      for (let i = 0; i < 13 - currentLastWeek; i++) {
        let day = []
        day[0] = m + 1
        day[1] = i + 1
        days.push(day)
      }
      this.calendar.daysList = days
      this.calendar.currentYear = y
      this.calendar.currentMonth = m
      this.calendar.currentDay = d
    },
    showArticle (n) {
      this.show = true
      this.childComponents = 'article' + n
      window.scroll(0, 0)
    },
    closeItem () {
      this.childComponents = undefined
      this.show = false
    }
  }
}
</script>

